import { Injectable } from '@angular/core';
import { HttpClient ,HttpHeaders} from '@angular/common/http';
import { Observable,of} from 'rxjs';
import { Hero } from './hero';
import { catchError, map, tap } from 'rxjs/operators';
import { MessageService } from './message.service';

@Injectable({
  providedIn: 'root'
})

export class HeroService {
// 请留意修改为你自己的URL
  private heroesUrl = 'api/heroes';
// 注入了HttpClient服务
httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};
  constructor(private httpClient: HttpClient,private messageService: MessageService) { }
// 获取所有英雄
  getHeroes(): Observable<Hero[]> {
    this.messageService.add('获取英雄信息');
    return this.httpClient.get<Hero[]>(this.heroesUrl);
  }
// 获取指定id的英雄
  getHeroById(id: string): Observable<Hero> {
    var now = new Date();
    this.messageService.add(`英雄详情 id：${id},时间：${now}`);
    return this.httpClient.get<Hero>(`${this.heroesUrl}/${id}`);
  }
// 获取所有的顶级英雄
  getTopHeroes(): Observable<Hero[]> {
    // 请参考Deployd的API文档
    var now = new Date();
    this.messageService.add(`获取所有顶级英雄  时间：${now}`);
    return this.httpClient.get<Hero[]>(`${this.heroesUrl}?isTop=true`);
  }
// 添加一个英雄
  addHero(hero: Hero): Observable<Hero> {
    var now = new Date();
    this.messageService.add(`添加英雄 id：${hero.id},时间：${now}`);
    return this.httpClient.post<Hero>(this.heroesUrl, hero);
  }
// 删除一个英雄
  deleteHero(id: string): Observable<any> {
    var now = new Date();
    this.messageService.add(`删除英雄 id：${id},时间：${now}`);
    return this.httpClient.delete(`${this.heroesUrl}/${id}`);
  }
// 更新一个英雄
  updateHero(hero: Hero): Observable<Hero> {
    var now = new Date();
    this.messageService.add(`修改英雄 id：${hero.id},时间：${now}`);
    return this.httpClient.put<Hero>(this.heroesUrl, hero);
  }
  //搜索一个英雄
  searchHeroes(term: string): Observable<Hero[]> {
    if (!term.trim()) {
      // if not search term, return empty hero array.
      return of([]);
    }
    var now = new Date();
    this.messageService.add(`搜索英雄信息：${term},时间：${now}`);
    return this.httpClient.get<Hero[]>(`${this.heroesUrl}/?name=${term}`)
  }
  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
  
      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead
  
      // TODO: better job of transforming error for user consumption
      // this.log(`${operation} failed: ${error.message}`);
  
      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }
}

